
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.container {
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 1px solid #CCC;
  padding: 30px;
  border-radius: 3px;
}
.btn {
  display: inline-block;
  background: #222;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 3px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
}
.btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}
.btn-block {
  display: block;
  width: 100%;
}
#nav {
  padding: 30px;
}
#nav a {
  font-weight: bold;
  color: #2c3e50;
}
#nav a.router-link-exact-active {
  color: #42b983;
}
